.spin1 > img {
    display: block; /* removes bottom margin/whitespace */
    -webkit-transition: -webkit-transform .8s ease-in-out;
    transition: transform .8s ease-in-out;
  }
  
  .spin1:hover > img {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
