@media only screen and (max-width: 1000px) 
   {
    .advert{
        display: grid;
        grid-template-columns: repeat(2,350px) ;
        grid-template-rows: 400px ;
        grid-gap: 2%;
    }
    
    .spacing{
       justify-content: center;
       margin-top: 10%;
    
    }
}
