@media only screen and (max-width: 1325px) 
{
    .ingrid{
        display: grid;
        grid-template-columns: repeat(auto-fill , 240px );
        grid-template-rows: 550px 550px;
        grid-gap: 2%;
        margin-left: 6%;
        margin-right: 6%;
    }
}

