/* mobilePhones */

/* Portrait */
@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2)
  and (orientation: portrait) {
   
    .ingrid{
        display: grid;
        grid-template-columns: repeat(2, 150px );
        grid-template-rows: 330px 330px 330px 330px ;
        grid-gap: 2%;
        margin-left: 6%;
        margin-right: 6%;
    }
}