@media only screen 
  and (max-width: 1000px) 
 { 

    .hchild{
      position: fixed;
      top:0;
      width: 100%;
      height: 10%;
      background-color: #ffffff;
      z-index:2;
  }
  .hidden{
     visibility: hidden;
  }
  .vanish{
    display: none;
  }
  .show{
    display:block;
  }
}

@media only screen 
  and (max-width: 1091px) 
 { 
.width{
  width:18%
}
}
@media only screen 
  and (max-width: 660px) 
 { 
.width{
  width:23%
}
}



/* mobilePhones */

/* Portrait */
@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2)
  and (orientation: portrait) {
    .hchild{
        position: fixed;
        top:0;
        width: 100%;
        height: 10%;
        background-color: #ffffff;
        z-index:2;
    }
    .hidden{
       visibility: hidden;
    }
    .vanish{
      display: none;
    }
    .width{
      width:30%
    }
}